<template>
  <div class="notifications-example modals-example">
    <header>
      <NavBar :to-display="{ 'selectionList': true, 'studentLink': true, 'teacherLink': true, 'contactLink': true, 'tosLink': true, 'aboutLink': true }" />
    </header>
    <div
      style="padding:20px;"
      class="examples"
    >
      <h2>notifications</h2>
      <!-- a @click functuions is a must. notifications html is in app.vue except custom notification. -->
      <!-- to change the position of the notification, it must be changed in the html in app.vue -->
      <button
        class="btn btn-default"
        @click="defaultNotification('default')"
      >default notifications, bottom left</button>
      <button
        class="btn btn-default"
        @click="defaultNotification('success')"
      >success notifications, top right</button>
      <button
        class="btn btn-default"
        @click="defaultNotification('error')"
      >err notifications, bottom right</button>
      <button
        class="btn btn-default"
        @click="defaultNotification('warn')"
      >warn notifications, top left</button>
      <button
        class="btn btn-default"
        @click="defaultNotification('custom-template')"
      >custom-template notifications, top right</button>
      <!--  custom notification -->
      <notifications
        group="custom-template"
        position="bottom right"
      >
        <template
          slot="body"
          slot-scope="props"
        >
          <div class="notification">
            <a class="title">
              {{ props.item.title }}
            </a>
            <a
              class="close"
              @click="props.close"
            >
              <i class="fa fa-fw fa-close" />
            </a>
            <div v-html="props.item.text" />
          </div>
        </template>
      </notifications>
    </div>
    <div class="dialogs">
      <!-- dialogs and modals are inserted as html in app.vue -->
      <h2>Dialogs</h2>
      <button @click="confirmModal()">custom confirm Dialog</button>
      <button @click="alertModal()">alert</button>
    </div>
    <div>
      <h2>modal</h2>
      <button @click="customModal">custom  modal</button>
    </div>
    <div class="spinner">
      <!-- cssClass is for background color for different cases.  -->
      <!-- spinner is the type. more can be inserted via spinner.vue -->
      <Spinner
        spinner="3"
        css-class="teacher"
      /> 
      <Spinner
        spinner="2"
        css-class="student"
      />
      <Spinner
        spinner="1"
        css-class="teacher"
      />
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import appConfig from '../config/dev-config';
//this is a must
import Notifications from '../services/notifications';
import ModalService from '../services/modals-service.js';
import Spinner from '@/components/spinner.vue';
import logger from '@/services/logger.js';

// notifications
//example: http://vue-notification.yev.io/
// docs: https://github.com/euvl/vue-notification/

// dialog & modal
//https://github.com/euvl/vue-js-modal/blob/master/README.md
// code examples : https://github.com/euvl/vue-js-modal/tree/master/demo/client_side_rendering/src
// live examples: http://vue-js-modal.yev.io/

export default {
  components: {
    NavBar,
    Spinner
  },
  computed: {
    confirmDialog() {
      //if user clicked confirmDialog this will be changed
      return this.$store.getters['confirmDialog']; // boolean
    }
  },
  watch: {
    confirmDialog() {
      //on confirmDialog logic here
      this.onConfirm();
    }
  },
  created() {
    if (!this.isDevMode()) {
      this.$router.push('/');
    }
  },
  methods: {
    //this is a must
    defaultNotification(group) {
      Notifications.notify(group, `title`, `text`);
    },
    isDevMode() {
      //to kick user out in prod or test env
      if (appConfig.currentMode === appConfig.options.devMode) {
        return true;
      } else {
        return false;
      }
    },
    //use call or bind to make the 'this' in the service vue's 'this'
    alertModal() {
      ModalService.alert.call(this, 'err');
    },
    async confirmModal() {
      const flag = await ModalService.confirmDialog.call(
        this,
        'are you sure?',
        'true'
      ); //return boolean
      if (flag) {
        logger.debug('user confirmed');
      }
    },
    customModal(e, content) {
      ModalService.customModal.call(this, 'content here!');
    },
    onConfirm() {
      // on confirmDialog logic here
      logger.debug('user confirmed');
    }
  }
};
</script>

<style lang="scss">
.modal-warpper .vue-dialog-button {
  //in order to have css affect the dialog, use a wrapper and be specific.
  background-color: white;
}

.notification {
  background-color: red;
  color: white;
}
</style>
