import axios from 'axios';
import apiConfig from './app-api';
import logger from '@/services/logger.js';

export default {
  getTeacherDummyData: async function(token) {
    const response = await axios.get(apiConfig.getTeacherDummyDataUrl(), {
      headers: {
        'Content-Type': 'application/json',
        'x-access-token': token
      }
    });
    return response.data;
  },
  switchActiveSchool: async function(data) {
    const response = await axios.post(
      apiConfig.getTeacherDummySwitchSchoolUrl(),
      { schoolId: data.schoolId },
      {
        headers: {
          'Content-Type': 'application/json',
          'x-access-token': data.token
        }
      }
    );
    return response.data;
  },
  uploadExcelFile: async function(data) {
    const response = await axios.post(
      apiConfig.getFileUploadUrl(),
      { file: data },
      {
        headers: {
          'Content-Type': 'application/json'
        }
      }
    );

    return response;
  },
  uploadJsonFile: async function() {
    const response = await axios.get(apiConfig.getJsonUploadUrl(), {
      headers: {
        'Content-Type': 'application/json'
      }
    });

    return response;
  },
  getStudentDataForManagmentTool: async function(token) {
    const response = await axios.get(apiConfig.getManagmentToolDataUrl(), {
      headers: {
        'Content-Type': 'application/json',
        'x-access-token': token
      }
    });

    return response;
  },
  deleteStudentByDocId: async function(token, studentDocId) {
    const response = await axios.post(
      apiConfig.getStudentDeleteUrl(),
      { studentDocId },
      {
        headers: {
          'Content-Type': 'application/json',
          'x-access-token': token
        }
      }
    );

    logger.debug(response);

    return response.data.success;
  }
};
