<template>
  <div class="teacher-data-list">

    <ul class="clean-list flex space-around align-center flex-column">
      <li 
        v-for="item in items" 
        v-if="items" 
        :key="item.id"
      >
        <TeacherDataPreview :item="item" />
      </li>
    </ul>


  </div>
    
</template>

<script>
import TeacherDataPreview from '@/components/teacher/TeacherDataPreview.vue';
export default {
  name: 'TeacherDataList',
  components: {
    TeacherDataPreview
  },
  props: [],
  data() {
    return {
      items: []
    };
  },
  computed: {
    itemInPath() {
      return this.$route.params.itemId;
    }
  },
  watch: {
    itemInPath() {
      this.$store.dispatch('teacher/setSelectedItemById', this.itemInPath);
    }
  },
  created() {
    this.loadItems();
  },
  methods: {
    loadItems() {
      this.items = this.$store.getters['teacher/queryToData'];
    }
  }
};
</script>

