import { promises } from 'fs';

/* eslint-disable */

export default {
    customModal (content) {
        this.$modal.show('custom', {content: 'fdsfds'});
    },
    alert(text){ // test: string
        this.$modal.show('dialog', {
            title: 'Alert!',
            text,
            buttons: [
              {
                title: 'Close'
              }
           ]
          })
    },
    confirmDialog(title, yes, no){ // v-dialog, title, yes & no are strings
      return new Promise((resolve, reject)=> {
        this.$modal.show('dialog', {
            title,
            buttons: [
              {
                title: yes || 'Yes',
                handler: () => { 
                    this.$modal.hide('dialog');
                    resolve(true);
                }
              },
              {
                title: no || 'No',
                handler: ()=> {
                  this.$modal.hide('dialog');
                  resolve(false);
                }
              }
           ]
          })
        })

    },
    hide () {
        this.$modal.hide('hello-world');
    }
}