<template>
  <div class="teacher-data-preview">
    <!-- <router-link :to="'teacher/'+item.id"> -->

    <div 
      class="card" 
      @click="goTodetail(item)"
    >
      <div class="title">
        <h2>
          {{ item.name }}
        </h2>
      </div>
      <div class="content">
        {{ item.description }}
      </div>
    </div>
    <!-- </router-link> -->
  </div>

</template>

<script>
export default {
  name: 'TeacherDataPreview',
  components: {},
  props: ['item'],
  methods: {
    goTodetail(item) {
      this.$store.dispatch('teacher/setSelectedItemById', item.id);
      this.$router.push('/Teacher/TeacherDataCategoryOne/' + item.id);
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 12px 16px 10px 10px;
  margin: 0px 10px 20px 10px;
  width: 100%;
  min-width: 300px;
  border-radius: 10px;

  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }
}
</style>
