import Vue from 'vue';
import Router from 'vue-router';
import Login from './views/Login.vue';
import IdmCallback from './views/IdmCallback.vue';
import SelectSchool from './components/teacher/SelectSchool.vue';
import TeacherDataDetails from './components/teacher/TeacherDataDetails.vue';
import TeacherDataCategoryOne from './components/teacher/TeacherDataCategoryOne.vue';
import Notifications from './components/notifications.example.vue';
import store from './store';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/IdmCallback',
      name: 'IdmCallback',
      component: IdmCallback
    },
    {
      path: '/idmcallback/:declaredPathSuffixForIdm',
      // name: 'idmcallback',
      component: IdmCallback,
      props: true
    },
    {
      path: '/About',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'about' */ './views/About.vue')
    },
    {
      path: '/tos',
      name: 'tos',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'tos' */ './views/TermsOfService.vue')
    },
    {
      path: '/contact',
      name: 'contact',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'contact' */ './views/Contact.vue')
    },
    {
      path: '/upload',
      name: 'ExcelUpload',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'ExcelUpload' */ './views/ExcelUpload.vue')
    },
    {
      path: '/json-upload',
      name: 'JsonUpload',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'ExcelUpload' */ './views/JsonUpload.vue')
    },
    {
      path: '/smt',
      name: 'StudentManagment',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'ExcelUpload' */ './views/StudentManagment.vue')
    },
    {
      path: '/Student',
      name: 'student',

      component: () =>
        import(/* webpackChunkName: 'student' */ './views/Student.vue'),
      beforeEnter: (to, from, next) => {
        let token = store.getters.getToken;
        let isStudent = store.getters.isStudent;
        if (token && isStudent) {
          next();
        } else {
          next('/');
        }
      }
    },
    {
      path: '/Teacher',
      component: () =>
        import(/* webpackChunkName: 'teacher' */ './views/Teacher.vue'),
      beforeEnter: (to, from, next) => {
        let token = store.getters.getToken;
        let isTeacher = store.getters.isTeacher;
        if (token && isTeacher) {
          if (to.name === 'selectSchool') {
            next();
          } else {
            // Check if already selected 'Active School'
            let activeSchool = store.getters['teacher/getActiveSchool'];
            if (activeSchool) {
              // activeSchool exist
              next();
            } else {
              // no activeSchool selected
              next('/Teacher');
            }
          }
        } else {
          next('/');
        }
      },
      children: [
        {
          path: '/',
          name: 'selectSchool',
          component: SelectSchool
        },
        {
          path: '/Teacher/TeacherDataCategoryOne',
          name: 'teacherDataCategoryOne',
          component: TeacherDataCategoryOne
        },
        {
          path: '/Teacher/TeacherDataCategoryOne/:itemId',
          name: 'details',
          component: TeacherDataDetails
        }
      ]
    },
    {
      path: '/notificationsExample',
      name: 'notificationsExample',
      component: Notifications
    },
    {
      path: '*',
      name: '404',

      component: () =>
        import(/* webpackChunkName: 'student' */ './views/404.vue')
    }
  ]
});
