<template>
  <div class="teacher-data-category-one">
    <h1>Teacher</h1>
    <div>
      <TeacherDataList v-if="items && items.length > 0" />
      <div v-else>No data for you.</div>
    </div>
    <div>
      <!-- <TeacherDataDetails /> -->
    </div>
  </div>
</template>

<script>
import TeacherDataList from '@/components/teacher/TeacherDataList.vue';

export default {
  name: 'TeacherDataCategoryOne',
  components: {
    TeacherDataList
  },
  data() {
    return {
      items: null
    };
  },
  created() {
    this.loadItems();
  },
  methods: {
    loadItems() {
      this.items = this.$store.getters['teacher/queryToData'];
    }
  }
};
</script>

