<template>
  <div class="nav-bar">

    <div 
      ref="toggleScreen" 
      class="toggle-screen" 
      @click="toggleNavMobile"
    />

    <div class="container flex space-between align-center flex-row-reverse">
      <div 
        ref="navMobile" 
        class="nav-mobile" 
        @click="toggleNavMobile"
      >
        <div class="bar1" />
        <div class="bar2" />
        <div class="bar3" />
      </div>
      <h1>
        <router-link to="/"><img 
          class="app-logo" 
          src="../assets/img/logo-desktop.png"
        ></router-link>
      </h1>

      <div class="flex-row-reverse align-center flex inline-list">
        <ul 
          ref="navDesktop" 
          class="nav-desktop align-center flex inline-list" 
          arial-label="Main navigation bar"
        >
          <li v-show="toDisplay.teacherLink">
            <router-link to="/teacher"> Teacher </router-link>
          </li>
          <li v-show="toDisplay.studentLink">
            <router-link to="/student"> Student </router-link>
          </li>
          <li v-show="toDisplay.contactLink">
            <router-link to="/contact"> Contact </router-link>
          </li>
          <li v-show="toDisplay.homeLink">
            <router-link to="/"> Home </router-link>
          </li>
          <li v-show="toDisplay.aboutLink">
            <router-link to="/about"> About </router-link>
          </li>
          <li v-show="toDisplay.logoutLink">
            <button @click="logoutIdm"> Logout </button>
          </li>
        </ul>

        <div class="dropdown" v-show="toDisplay.selectionList">
          <div 
            class="dropbtn" 
            @click="toggleDropDown"
          >{{ activeSchoolName || 'schools' }} ⇓
            <i class="fa fa-caret-down" />
          </div>
          <div 
            ref="dropBtn" 
            class="dropdown-content"
          >
            <span 
              v-for="school in schools" 
              v-if="schools" 
              :key="school.schoolId"
            >
              <a 
                v-if="school.schoolName" 
                href="#" 
                @click="navBarSchoolSwitch(school)"
              >{{ school.schoolName }}</a>
            </span>
          </div>
          <span 
            v-if="!!fullName" 
            class="full-name"
          >
            שלום {{ fullName }}
          </span>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
import generalService from '../services/general-service.js';

export default {
  name: 'NavBar',
  // toDisplay: { item to display (links in nav, list, etc..): boolean }
  props: ['toDisplay'],  // all links must be included in toDisplay in every iteretion of the navbar!!
  data() {
    return {
      navBarMObile: false,
      schools: [],
      activeSchoolName: null
    };
  },
  computed: {
    fullName() {
      return this.$store.getters.getFullName;
    },
    getActiveSchool() {
      return this.$store.getters['teacher/getActiveSchool'];
    }
  },
  watch: {
    getActiveSchool() {
      this.getActiveSchoolName();
    }
  },
  created() {
    this.getScools();
    this.getActiveSchoolName();
  },
  methods: {
    getScools() {
      this.schools = this.$store.getters['teacher/getSchoolList'];
    },
    getActiveSchoolName() {
      let activeSchool = this.$store.getters['teacher/getActiveSchool'];
      if (activeSchool) {
        this.activeSchoolName = activeSchool.schoolName;
      }
    },

    toggleNavMobile() {
      this.$refs.navMobile.classList.toggle('change');
      this.$refs.navDesktop.classList.toggle('open');
      this.$refs.toggleScreen.classList.toggle('show-div');
    },
    toggleDropDown() {
      this.$refs.dropBtn.classList.toggle('drop-block');
    },
    async logoutIdm() {
      await generalService.logoutIdm();
    },
    navBarSchoolSwitch: async function(school) {
      let token = this.$store.getters.getToken;

      if (school) {
        // Selection is valid, go on
        let data = { token: token, schoolId: school.schoolId };
        let newToken = await this.$store.dispatch('teacher/switchSchool', data);
        if (newToken) {
          this.$store.commit('setToken', newToken);
          this.toggleDropDown();
          this.$router.push('/Teacher/');
        }
      } else {
        // Selection is invalid. warn user
      }
    }
  }
};
</script>

<style scoped lang="scss">
.drop-block {
  display: block !important;
}

.full-name {
  float: left;
  color: white;
  padding-left: 14px;
}

.drop-down {
  color: white;
}

.dropdown {
  overflow: hidden;
}

.dropdown .dropbtn {
  width: 180px;
  text-align: left;
  font-size: 17px;
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
  cursor: pointer;
}

.dropdown-content {
  background-color: #e03939;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 0;

  background-color: #222222;
  display: none; // hide until menu is clicked
  list-style: none; // replace with %styled-list
  margin: 0; // replace with %styled-list
  padding: 0; // replace with %styled-list
  position: absolute;
  width: 100%;
  max-width: 220px;
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.nav-bar {
  background-color: #333;
  padding: 5px;

  a {
    color: #fff;
    text-decoration: none;

    &:hover {
      color: #999;
    }
  }

  h1 {
    text-decoration: none;
    margin: 0 10px;
    font-size: 1.4em;
    font-weight: bold;
    font-family: 'openSans', 'Geneva', 'Tahoma', 'sans-serif';
  }

  ul {
    margin: 0px;

    li {
      padding: 1.5rem 1rem;
      font-weight: bold;

      a {
        text-decoration: none;

        &.router-link-exact-active {
          color: #999;
        }
      }

      button {
        font-family: 'Roboto', 'OpenSans', 'Geneva', 'Tahoma', 'sans-serif';
        font-weight: bold;
        color: #fff;
        border-color: #fff;

        &:hover {
          color: #999;
          background-color: rgba(0, 0, 0, 0.5);
        }
      }
    }
  }
}
.nav-mobile {
  display: none;
}
.toggle-screen {
  display: none;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background: rgba(42, 42, 42, 0.6);
  z-index: 0;
  opacity: 0.9;
  visibility: hidden;
  cursor: pointer;
  transition: opacity 0.5s;
}
@media (max-width: 720px) {
  .dropdown-content {
    width: 100vw;
    max-width: 100%;
    right: 0;
    //  max-width=
  }

  .app-logo {
    content: url('../assets/img/logo-mobile-header.png');
    height: 60px;
  }

  .show-div {
    z-index: 1;
    visibility: visible;
    opacity: 1;
    display: block;
  }
  .nav-desktop {
    margin: 0;
    z-index: 1;
    padding-top: 20px;
    flex-direction: column;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.9);
    border-left: 1px solid rgba(100, 100, 100, 0.5);
    min-width: 200px;
    transform: translate(100%, 0);
    transition: all 0.8s;

    &.open {
      transform: translate(0, 0);
    }

    li a {
      border-bottom: 2px solid black;
      text-decoration: dotted;
      display: block;
      line-height: 60px;
      text-align: center;
      font-size: 16px;
    }

    li a:hover {
      color: #999;
    }
  }

  /* nav-mobile button */

  .nav-mobile {
    z-index: 3;
    cursor: pointer;
    display: block;
    float: right;
    padding: 10px;
  }

  .bar1,
  .bar2,
  .bar3 {
    width: 35px;
    height: 5px;
    background-color: #fff;
    margin: 6px 0;
    transition: 0.4s;
  }

  /* Rotate first bar */
  .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  /* Fade out the second bar */
  .change .bar2 {
    opacity: 0;
  }

  /* Rotate last bar */
  .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }
}
</style>
